.footer_icons {
  padding: 1rem;
  background-color: #ef9b0f;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container_footer {
  height: 0;
}

.footer_icons li {
  list-style-type: none;
  padding: 0 1rem;
}

.footer_icons ul {
  display: flex;
  justify-content: center;
}

.android,
.ruby,
.Apple,
.mozilla {
  width: 40px;
  height: 40px;
  color: black;
}
